/**
 * localStorage
 * @param id   id
 * @param key  
 * @param value  
 */
export function saveToLocal(id, key, value) {
  let seller = window.localStorage.__seller__;
  if (!seller) {
    seller = {};
    seller[id] = {};
  } else {
    seller = JSON.parse(seller);
    if (!seller[id]) {
      seller[id] = {};
    }
  }
  seller[id][key] = value;
  window.localStorage.__seller__ = JSON.stringify(seller);
}

/**
 * localStorage
 * @param id   id
 * @param key  
 * @param def  
 */
export function loadFromLocal(id, key, def) {
  let seller = window.localStorage.__seller__;
  if (!seller) {
    return def;
  }
  seller = JSON.parse(seller)[id];
  // 
  if (!seller) {
    return def;
  }
  //  key
  let ret = seller[key];
  return ret || def;
}
